<template>
  <div class="FamilyPage">
    <assets-card
      color="#36d7b7"
      icon="gold-coin"
      title=""
      :value="amount"
      @XClick="clickAssets"
    ></assets-card>
    <assets-card color="#007acc" icon="bill" title="" @XClick="clickBill"></assets-card>
    <assets-card color="#dd5246" icon="add" title="" @XClick="doMakeBill"></assets-card>
  </div>
</template>

<script>
import AssetsCard from "@/components/homeViews/comp/AssetsCard.vue";

export default {
  name: "FamilyPage",
  components: {
    AssetsCard
  },
  data() {
    return {
      amount: ""
    };
  },
  methods: {
    clickAssets() {
      console.log("");
    },
    clickBill() {
      this.$router.push("/familybill");
    },
    doMakeBill() {
      this.$router.push("/acountingpage?type=family");
    }
  },
  created() {
    this.axios(
      this.axiosConfig("get", "fassets/getamount", {
        familyid: this.$store.state.familyID
      })
    ).then(res => {
      // console.log(res);

      if (res.data.amount) {
        this.amount = res.data.amount.toFixed(2);
      }
    });
  }
};
</script>

