<template>
  <div class="userInfo">
    <van-nav-bar title="" left-text="" left-arrow @click-left="onClickBack" />
    <van-form @submit="onSubmit" class="margin-top-10px">
      <!-- ===================== -->
      <van-field
        v-model="nickname"
        name=""
        label=""
        placeholder=""
        :rules="[
            { 
              required: true,
              message: '' 
            },
            {
              validator: validateNickname,
              message: '8'
            }]"
      />
      <van-field name="uploader" label="">
        <template #input>
          <van-uploader
            v-model="fileList"
            multiple
            :max-count="1"
            :before-read="beforeRead"
            :after-read="afterRead"
          />
        </template>
      </van-field>
      <!-- ======================= -->
      <!-- ======button===== -->
      <div style="margin: 16px 32px;">
        <van-button round block type="info" native-type="submit"></van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import upLoaderImg from "@/api/upLoaderImg";

export default {
  name: "UserInfo",
  data() {
    return {
      fileList: this.avatarUrl(),
      imgFile: {},
      isChangeImg: false,
      isChangeNickname: false,
      newAvatar: "",
      nickname: ""
    };
  },
  methods: {
    onClickBack() {
      this.$router.goBack();
    },
    avatarUrl() {
      return [
        {
          url: this.$store.state.avatar,
          isImage: true
        }
      ];
    },
    beforeRead(file) {
      //
      if (file.type !== "image/jpeg" && file.type !== "image/png") {
        this.$toast("jpg/png");
        return false;
      }
      return true;
    },
    afterRead(file) {
      this.imgFile = file.file;
      this.isChangeImg = true;
    },
    async onSubmit() {
      // 
      // 
      if (!this.isChangeNickname && !this.isChangeImg) {
        return;
      }

      // 
      if (this.imgFile && this.isChangeImg) {
        await upLoaderImg(this.imgFile)
          .then(data => {
            this.newAvatar = data.url;
          })
          .catch(err => {
            console.log(err);
          });
      }
      //url
      this.requestChange()
        .then(response => {
          console.log(response);
          if (response.data) {
            // 
            this.$dialog
              .alert({
                message: "",
                confirmButtonText: ""
              })
              .then(() => {
                this.$store.commit("change");
              });
          } else {
            this.$toast.fail(response.data.msg || "");
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    requestChange() {
      return this.axios(
        this.axiosConfig("post", "user/update_info", {
          uname: this.$store.state.uname,
          nickname: this.nickname,
          avatar: this.newAvatar
        })
      );
    },
    validateNickname(val) {
      return val.length <= 8;
    }
  },
  watch: {
    nickname: function() {
      this.isChangeNickname = true;
    }
  },
  created() {
    this.nickname = this.$store.state.nickname;
  }
};
</script>

<style lang="less" scoped>
@import "@/assets/style/mixins.less";

.userInfo {
  .lightGreyBg();
}
</style>