<template>
  <transition name="van-slide-right">
    <div class="Register">
      <van-nav-bar title="" left-text="" left-arrow @click-left="onClickBack" />
      <!-- =====form===== -->
      <van-form @submit="onSubmit" class="regForm">
        <van-field
          v-model="uname"
          name=""
          label=""
          placeholder=""
          :rules="[
            { 
              required: true,
              message: '' 
            },
            {
              validator: validateUname,
              message: '6-8'
            },
            {
              validator: asyncValidateUnameExist,
              message: ''
            },
            
          ]"
        />
        <!-- ===================== -->
        <van-field
          v-model="nickname"
          name=""
          label=""
          placeholder=""
          :rules="[
            { 
              required: true,
              message: '' 
            },
            {
              validator: validateNickname,
              message: '10'
            }]"
        />
        <van-field name="uploader" label="">
          <template #input>
            <van-uploader
              v-model="fileList"
              multiple
              :max-count="1"
              :before-read="beforeRead"
              :after-read="afterRead"
            />
          </template>
        </van-field>
        <!-- ======================= -->
        <van-field
          v-model="passwd"
          type="password"
          name=""
          label=""
          placeholder=""
          :rules="[{ required: true, message: '' }
          ,{ validator: validatePasswd, message: '6-15' }]"
        />
        <!-- ======================== -->
        <van-field
          v-model="reTypePasswd"
          type="password"
          name=""
          label=""
          placeholder=""
          :rules="[{ required: true, message: '' },
          { validator: validateRePasswd, message: '' }]"
        />
        <!-- ======button===== -->
        <div style="margin: 16px 32px;">
          <van-button round block type="info" native-type="submit"></van-button>
        </div>
      </van-form>
    </div>
  </transition>
</template>

<script>
import upLoaderImg from "@/api/upLoaderImg";

export default {
  name: "Register",
  components: {},
  data() {
    return {
      uname: "",
      nickname: "",
      passwd: "",
      reTypePasswd: "",
      avatar: "",
      fileList: [],
      imgFile: null
    };
  },
  methods: {
    onClickBack() {
      this.$router.goBack();
    },
    async onSubmit() {
      // toast
      this.$toast.loading({
        duration: 0, //  toast
        message: "...",
        forbidClick: true
      });

      // 
      // 
      if (this.imgFile) {
        await upLoaderImg(this.imgFile).then(data => {
          this.avatar = data.url;
        });
      }
      //url
      this.requestReg()
        .then(() => {
          // 
          this.$toast.clear();
          // 
          this.$dialog
            .alert({
              message: "",
              confirmButtonText: ""
            })
            .then(() => {
              this.$router.goBack();
            });
        })
        .catch(error => {
          console.log(error);
        });
    },
    beforeRead(file) {
      //
      if (file.type !== "image/jpeg" && file.type !== "image/png") {
        this.$toast("jpg/png");
        return false;
      }
      return true;
    },
    afterRead(file) {
      this.imgFile = file.file;
    },
    requestReg() {
      return this.axios(
        this.axiosConfig("post", "user/reg", {
          uname: this.uname,
          passwd: this.passwd,
          nickname: this.nickname,
          avatar: this.avatar
        })
      );
    },

    // 
    asyncValidateUnameExist(val) {
      return new Promise(resolve => {
        this.axios(
          this.axiosConfig("get", "user/check_uname_exist", {
            uname: val
          })
        )
          .then(res => {
            resolve(!res.data);
          })
          .catch(err => {
            console.log(err);
          });
      });
    },
    validateUname(val) {
      return /^[a-zA-Z][a-zA-Z0-9]{5,11}$/.test(val);
    },
    validatePasswd(val) {
      return /^[a-zA-Z0-9]{6,15}$/.test(val);
    },
    validateRePasswd(val) {
      return val === this.passwd;
    },
    validateNickname(val) {
      return val.length <= 10;
    }
  }
};
</script>

<style lang="less" scoped>
.Register {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #f2f2f2;
}

.regForm {
  margin-top: 12px;
}
</style>
