//upLoaderImg.js
import axios from 'axios';		//axios
import { Toast } from 'vant';	//Toast

function upLoaderImg(file) {	//file 
  //new FormData
  let params = new FormData();
  params.append('avatar', file);
  let config = {
    headers: { //
      'Content-Type': 'multipart/form-data'
    }
  };
  return new Promise((resolve, reject) => {
    // uploadUrl  
    axios.post(axios.defaults.baseURL + '/upload', params, config).then(res => {
      if (res && res.data && res.data.status === 200) {				// resolve
        resolve(res.data);
      } else {
        // Toast 
        Toast.fail(res.data && (res.data.msg));
        reject(res.data);
      }
    }).catch(err => {
      Toast.fail('');
      reject(err);
    });
  });
}
export default upLoaderImg;

