<template>
  <div class="ChangePasswd">
    <van-nav-bar title="" left-text="" left-arrow @click-left="onClickBack">
      <template #right>
        <van-button type="primary" size="mini" color="#644edd" @click="onComplete"></van-button>
      </template>
    </van-nav-bar>
    <!-- ======form====== -->
    <van-form @submit="onSubmit" ref="chPwdForm" class="chPwdForm">
      <van-field name="" label="" :placeholder="uname" disabled />
      <van-field
        v-model="oldPasswd"
        type="password"
        name=""
        label=""
        placeholder=""
        :rules="[{ required: true, message: '' }]"
      />
      <!-- ======================= -->
      <van-field
        v-model="passwd"
        type="password"
        name=""
        label=""
        placeholder=""
        :rules="[{ required: true, message: '' }
          ,{ validator: validatePasswd, message: '6-15' }]"
      />
      <!-- ======================== -->
      <van-field
        v-model="reTypePasswd"
        type="password"
        name=""
        label=""
        placeholder=""
        :rules="[{ required: true, message: '' },
          { validator: validateRePasswd, message: '' }]"
      />
    </van-form>
  </div>
</template>

<script>
export default {
  name: "ChangePasswd",
  data() {
    return {
      oldPasswd: "",
      passwd: "",
      reTypePasswd: "",
      checkResult: false
    };
  },
  computed: {
    uname() {
      return this.$store.state.uname;
    }
  },
  methods: {
    onClickBack() {
      this.$router.goBack();
    },
    async onSubmit() {
      // 
      await this.checkOldPasswd();
      if (this.checkResult === true) {
        // post
        this.uploadChange();
      } else if (this.checkResult === "error") {
        this.$toast({
          type: "fail",
          message: ""
        });
      } else {
        this.$dialog.alert({
          message: ""
        });
      }
    },
    onComplete() {
      this.$refs.chPwdForm.submit();
    },
    // 
    async checkOldPasswd() {
      await this.axios(
        this.axiosConfig("get", "user/check_old_passwd", {
          uname: this.uname,
          oldpasswd: this.oldPasswd
        })
      )
        .then(response => {
          console.log(response);
          // ,truefalse
          this.checkResult = response.data;
        })
        .catch(error => {
          console.log(error);
          this.checkResult = "error";
        });
    },
    // post
    uploadChange() {
      this.axios(
        this.axiosConfig("post", "user/update_passwd", {
          uname: this.uname,
          passwd: this.passwd
        })
      )
        .then(response => {
          console.log(response);
          // ,truefalse
          let changeResult = response.data;
          if (changeResult) {
            this.$dialog
              .alert({
                message: ""
              })
              .then(() => {
                // 
                localStorage.removeItem("uname");
                this.$router.push("/");
              });
          }
        })
        .catch(error => {
          console.log(error);
          this.$toast({
            type: "fail",
            message: ""
          });
        });
    },

    // 
    validatePasswd(val) {
      return /^[a-zA-Z0-9]{6,15}$/.test(val);
    },
    validateRePasswd(val) {
      return val === this.passwd;
    }
  }
};
</script>

<style lang="less" scoped>
@import "@/assets/style/mixins.less";

.ChangePasswd {
  .lightGreyBg();

  .chPwdForm {
    margin-top: 10px;
  }
}

.van-button--mini {
  font-size: 12px;
  font-weight: 700;
  min-width: 40px;
}
</style>