<template>
  <div class="manage">
    <my-nav-bar title=""></my-nav-bar>
    <van-cell-group class="margin-top-10px">
      <van-cell title="" :value="familyName" is-link @click="openNameDialog" />
      <van-cell title="" :value="familyID" is-link @click="openIdDialog" />
      <van-cell title="" is-link @click="openUnBindDialog" />
    </van-cell-group>
    <!-- =========== -->
    <van-dialog
      v-model="NameDialogShow"
      title=""
      show-cancel-button
      close-on-popstate
      :before-close="beforeNameClose"
    >
      <van-form
        class="FamilyNameForm"
        ref="NameForm"
        @submit="FamilyNameSubmit"
        @failed="onNameSubmitFailed"
      >
        <van-field
          class="FamilyNameField"
          v-model="familyName"
          name=""
          placeholder=""
          clearable
          :rules="[
            { 
              required: true,
              message: '' 
            },
            {
              validator: validateFamilyname,
              message: '8'
            }]"
        />
      </van-form>
    </van-dialog>
    <!-- ========== -->
    <van-dialog
      v-model="unBindDialogShow"
      title=""
      close-on-popstate
      show-cancel-button
      @confirm="onUnBindConfirm"
    >
      <div class="unBindDialogContainer">
        <span></span>
        <span class="fnameText">{{ familyName }}</span>
        <span></span>
      </div>
    </van-dialog>
  </div>
</template>

<script>
import MyNavBar from "@/components/MyNavBar.vue";
export default {
  name: "FamilyManage",
  components: {
    MyNavBar
  },
  data() {
    return {
      familyName: "",
      familyID: "",
      NameDialogShow: false,
      unBindDialogShow: false,
      isFamilyNameChanged: false,
      done: function() {}
    };
  },
  methods: {
    openNameDialog() {
      this.NameDialogShow = true;
    },
    openIdDialog() {
      this.$dialog
        .alert({
          title: "",
          message: this.familyID,
          confirmButtonText: ""
        })
        .then(() => {
          this.copy(this.familyID);
        });
    },
    openUnBindDialog() {
      this.unBindDialogShow = true;
    },
    // 
    copy(str) {
      var save = function(e) {
        e.clipboardData.setData("text/plain", str); //clipboardData
        e.preventDefault(); //
      };
      document.addEventListener("copy", save);
      return document.execCommand("copy"); //
    },
    validateFamilyname(val) {
      return val.length <= 8;
    },
    // 
    FamilyNameSubmit() {
      this.axios(
        this.axiosConfig("post", "family/update_name", {
          familyid: this.familyID,
          familyname: this.familyName
        })
      )
        .then(response => {
          console.log(response);
          if (!response.data) {
            return new Promise(new Error("res send a false"));
          }
          this.$toast({
            message: "",
            position: "bottom"
          });
          // 
          this.$refs.NameForm.resetValidation();
          this.$store.commit("change");
          this.$store.commit("Fchange");
          this.done();
        })
        .catch(err => {
          console.log(err);

          // ;
          this.$toast({
            message: "",
            position: "bottom"
          });
          this.done(false);
        });
    },
    // 
    onNameSubmitFailed() {
      this.done(false);
    },
    // 
    beforeNameClose(action, done) {
      // 
      this.done = done;
      // console.log("beforeclose");
      // 
      if (action === "cancel") {
        this.$refs.NameForm.resetValidation();
        done();
        return;
      }
      // 
      if (this.isFamilyNameChanged) {
        // 
        this.$refs.NameForm.submit();
      } else {
        // 
        done(false);
      }
    },
    onUnBindConfirm() {
      this.axios(
        this.axiosConfig("post", "/family/unbind", {
          uname: this.$store.state.uname,
          familyid: this.familyID
        })
      )
        .then(response => {
          console.log(response);
          if (response.data) {
            this.$toast({
              message: "",
              position: "bottom"
            });
            localStorage.removeItem("familyID");
            this.$router.push("/home/family");
          } else {
            return Promise.reject(new Error("some errors"));
          }
        })
        .catch(err => {
          console.log(err);
          this.$toast({
            message: "",
            position: "bottom"
          });
        });
    }
  },
  watch: {
    familyName() {
      this.isFamilyNameChanged = true;
    }
  },
  created() {
    this.familyName = this.$store.state.familyName;
    this.familyID = this.$store.state.familyID;
  }
};
</script>

<style lang="less" scoped>
@import "@/assets/style/mixins.less";

.manage {
  .lightGreyBg();
}

.FamilyNameForm {
  margin: 10px;
  .FamilyNameField {
    background-color: rgba(7, 17, 27, 0.1);
    border-radius: 10px;
  }
}

.unBindDialogContainer {
  margin: 20px;
  color: rgba(7, 17, 27, 0.8);

  .fnameText {
    display: inline-block;
    margin: 0 10px;
    color: #000;
    font-weight: bold;
  }
}
</style>