<template>
  <div class="create">
    <my-nav-bar-with-right-button title="" button-context="" @XClick="onCreate"></my-nav-bar-with-right-button>
    <!-- =====form===== -->
    <van-form class="margin-top-10px" ref="CreateFamilyForm" @submit="onSbumit">
      <!-- ===================== -->
      <van-field
        v-model="familyName"
        name=""
        label=""
        placeholder=""
        :rules="[
            { 
              required: true,
              message: '' 
            },
            {
              validator: validateFamilyName,
              message: '8'
            }]"
      />
    </van-form>
  </div>
</template>

<script>
import MyNavBarWithRightButton from "@/components/MyNavBarWithRightButton.vue";

export default {
  name: "CreateFamily",
  components: {
    MyNavBarWithRightButton
  },
  data() {
    return {
      familyName: ""
    };
  },
  methods: {
    onCreate() {
      console.log("create");
      this.$refs.CreateFamilyForm.submit();
    },
    validateFamilyName(val) {
      return val.length <= 8;
    },
    onSbumit() {
      this.axios(
        this.axiosConfig("post", "family/create", {
          creator: this.$store.state.uname,
          familyname: this.familyName
        })
      )
        .then(response => {
          console.log(response);

          if (response.data.familyID && !response.data.err) {
            this.$toast.success("");
            // familyID
            localStorage.setItem("familyID", response.data.familyID);
            this.$store.commit("change");
            this.$store.commit("Fchange");
            this.$router.push("/home/family");
          } else {
            this.$toast.fail("");
          }
        })
        .catch(err => {
          console.log(err);
          this.$toast.fail("");
        });
    }
  }
};
</script>

<style lang="less" scoped>
@import "@/assets/style/mixins.less";

.create {
  .lightGreyBg();
}
</style>
